.ebook_view_main {
    display: flex;
    flex-direction: column;
    align-items: center;
   /* padding: relative;*/
    margin-left: 160px;
    margin-right: 20px;
    /* vh means display height */
    min-height: calc(100vh - 165px);
    margin-top: 100px;
    margin-bottom: 20px;
    gap: 1rem;
    background: white;

    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}
.ebook_view_main table {
    border-collapse: collapse;
    border:1px solid black;
}
.ebook_view_main table td {
    border:1px solid black;
    padding: 10px;
}
.ebook_view_main table th {
    border:1px solid black;
    padding: 10px;
}